.cards {
    width: 100%;
    display: flex;
}

.cards__card {
    height: rem(360px);
    flex-grow: 1;
    width: 33%;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: rem(40px) 0 ;
    border: 3px solid $line;
    margin-left: -3px;
    position: relative;

    &:first-child {
        margin-left: 0;
    }

    &:hover {
        border: 3px solid $accent;
        box-shadow:  20px 20px 100px 0px rgba(#000, 0.2);
        z-index: 5;

        .cards__link {
            color: $accent;
        }
    }
}

.cards__text {
    font-size: rem(24px);


    &--bold {
        font-size: rem(30px);
        font-weight: 800;
        color: $basic;
    }

    &--gray {
        font-size: rem(16px);
        opacity: 0.5;
        text-decoration: underline;
    }
}

.cards__icon {
    margin-top: auto;
    margin-bottom: auto;

    &--phone {
        width: rem(119px);
        height: rem(119px);
        @include sprite($phone-big)
    }

    &--envelope {
        width: rem(119px);
        height: rem(83px);
        @include sprite($envelope-big)
    }

    &--point {
        width: rem(90px);
        height: rem(120px);
        @include sprite($marker-big)
    }
} 

.cards__link {
    text-decoration: underline;
    font-size: rem(16px);
    font-weight: 800;
}